export default {
  name: 'ToggleDark',
  data() {
    return {
      isDark: this.$store.getters.useDarkMode
    };
  },
  methods: {
    handleToggleClick() {
      this.isDark = !this.isDark;
      this.$store.commit('setUseDarkMode', this.isDark);
      this.$store.commit('setCusDarkMode', true);
    }
  }
};